import React,{ useState } from "react";
import "./assets/css/custom.css";
import DataValidator from "./SecondaryComponent/DataValidator";
// import { LinkedIn } from "@react-icons/all-files";
// import { Twitter } from "@material-ui/icons";
// import { GitHub } from "@material-ui/icons";
// import { Facebook } from "@material-ui/icons";
import { toast } from "react-toastify";
import axios from "axios";




export default function Footer() {

  const [subscriber, setSubscriber] = useState({
    email: "",
  }); 
  const { email } = subscriber;

  const onInputChange = (e) => {
    DataValidator(e.target);
    setSubscriber({ [e.target.name]: e.target.value });
  };
  const onSubmit = async (e) => {
    e.preventDefault();

    toast.success(
      `Hi ${email.split("@")[0]},\n You have successfully subscribed!`);

    await axios.post("http://localhost:5002/subscribers/add-new-subscriber", subscriber);
    // setTimeout(() => {nav("/about")}, 2000);
  };





  return (
      <footer className="footer fade-in">
        <div className="component-container">
          <div className="component-body">
          <div className="component-body-left">
          <h3>Social Media</h3>
          <div className="social-media">
            <div className="social-media-links">
              {/* <a className="social-media-link" href="http://www.google.com"><span className="social-media-logo"><LinkedIn/></span> <span className="social-media-link-text">LinkedIn</span> </a> */}
              {/* <a className="social-media-link" href="http://www.google.com"><span className="social-media-logo"><Twitter/></span> <span className="social-media-link-text">Twitter</span> </a> */}
              {/* <a className="social-media-link" href="http://www.google.com"><span className="social-media-logo"><GitHub/></span> <span className="social-media-link-text">Github</span> </a> */}
              {/* <a className="social-media-link" href="http://www.google.com"><span className="social-media-logo"><Facebook/></span> <span className="social-media-link-text">Facebook</span> </a> */}
            </div>
          </div>
          </div>
          <div className="component-body-middle">
          <h3>Site Map</h3>
          </div>
          <div className="component-body-right">
          <h3>Stay Connected</h3>
          <form onSubmit={(e) => onSubmit(e)} className="footer-email-subscribe-section">
              <input type="email"  name="email" onChange={onInputChange} className="form-control footer-email-input" placeholder="Email address"/>
              <button className="footer-subscribe-button">Subscribe</button>
          </form>
          </div>
          </div>
        </div>
      </footer>
  );
}
