import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import CountryCode from "./SecondaryComponent/CountryCode";
import DataValidator from "./SecondaryComponent/DataValidator";
import "./assets/css/custom.css";

export default function Contact(props) {
  let nav = useNavigate();
  var currentDateAndTime;
  const [contact, setContact] = useState({
    datemilliseconds:"",
    name: "",
    email: "",
    country:{code:"",name:""},
    mobile: "",
    subject: "",
    description: ""
  });
  const { name, email, mobile, subject, description } = contact;

  const onInputChange = (e) => {
    DataValidator(e.target);
    setContact({ ...contact, [e.target.name]: e.target.value });
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    currentDateAndTime=(+new Date()).toString();
    contact["country"]["code"]=document.querySelector("#countryCode").value;
    contact["country"]["name"]=document.querySelector("#countryCode")[document.querySelector("#countryCode").selectedIndex].innerText;
    contact["datemilliseconds"]=currentDateAndTime

    toast.success(
      `Hi ${name},\n Your details has been sent successfully! \n We will`);

    await axios.post("http://localhost:5002/contacts/add-new-contact", contact);
    // setTimeout(() => {nav("/about")}, 2000);
  };

  return (
    <div className="fade-in">
      <div className="container">
        <div className="row">
          <div className="component-heading">Get In Touch</div>
          <div className="col-md-6">
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1012.0936420799104!2d73.9076974487763!3d18.561786630847504!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2c13ec1a867d5%3A0xa0d9c682e747f663!2sLalwani%20Avenue!5e0!3m2!1sen!2sin!4v1626367565606!5m2!1sen!2sin"
              height={500}
              width="100%"
              title="address-map"
              className="contactMap"
              style={{ border: 0 }}
              allowFullScreen
              loading="lazy"
            />
          </div>

          <div className="col-md-6">
            <form className="row g-3" onSubmit={(e) => onSubmit(e)}>
              <div>
                <div className="mb-3">
                  <label className="form-label">Name</label>
                  <input
                    name="name"
                    type="text"
                    className="form-control"
                    placeholder="Your name"
                    value={name}
                    onChange={onInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Email address</label>
                  <input
                    name="email"
                    type="email"
                    className="form-control"
                    placeholder="Your email"
                    value={email}
                    onChange={onInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Mobile</label>
                  <div className="mobile-input">
                    <CountryCode/>
                  <input
                    name="mobile"
                    type="text"
                    className="form-control"
                    placeholder="Your mobile"
                    value={mobile}
                    onChange={onInputChange}
                  /></div>
                </div>
                <div className="mb-3">
                  <label className="form-label">Subject</label>
                  <input
                    name="subject"
                    type="text"
                    className="form-control"
                    placeholder="Enter subject"
                    value={subject}
                    onChange={onInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Comment</label>
                  <textarea
                    name="description"
                    className="form-control"
                    rows={3}
                    placeholder="Enter description"
                    value={description}
                    onChange={onInputChange}
                  />
                </div>
                <div className="col-12">
                  <button
                    className="btn contact-form-submit-btn"
                  >
                    Submit
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

