import React from 'react'


export default function ThemeColorSelector() {

const changeColor =(e) => {
document.documentElement.style.setProperty('--primary-color', e.target.value);
document.documentElement.style.setProperty('--secondary-color', e.target.value);

}
const darkTheme =(e) => {
    var documentStyle=document.documentElement
    let white=getComputedStyle(document.documentElement).getPropertyValue('--white-color')
    let darkGray=getComputedStyle(document.documentElement).getPropertyValue('--darkgray-color')
    let black=getComputedStyle(document.documentElement).getPropertyValue('--black-color')

    documentStyle.style.setProperty('--darkgray-color', white);
    documentStyle.style.setProperty('--white-color', darkGray);
    documentStyle.style.setProperty('--black-color', white);
    console.log(getComputedStyle(document.documentElement).getPropertyValue('--darkgray-color'))

    if(e.target.innerText=="Dark Theme"){
        e.target.innerText="Light Theme"
    }else
    {
        e.target.innerText="Dark Theme"
    }
}
const backgroundToggle = (e) =>{
    let bgVideo=document.querySelector(".websiteBackgroundVideo")
    let bgVideooverlay=document.querySelector(".websiteBackgroundVideoOverlay")
    let bodyBackground=document.querySelector("body")

    let bgVideoDisplayValue=getComputedStyle(bgVideo).display
    let bgVideoOverlayDisplayValue=getComputedStyle(bgVideooverlay).display
    
    if(e.target.value=="none"){
        bgVideo.style.display="block";
        bgVideooverlay.style.display="block";
        bodyBackground.style.background="transparent"
        e.target.innerText="Remove Background Video"
        e.target.value="block"
    }else{
        bgVideo.style.display="none";
        bgVideooverlay.style.display="none";
        e.target.innerText="Add Background Video"
        e.target.value="none"
    }
}
  return (
    <div id='themeColorSelector'>
        <div className='theme-color-selector-heading'>Select Theme Color</div> 
        <div className='theme-color-input-field'> <input type='color' className='theme-color-input' onChange={changeColor}/> </div> 
        <button className='dark-theme' onClick={darkTheme}> Dark Theme</button><br/>
        <button className='background-video-toggle-button' onClick={backgroundToggle} value="none"> Add Background Video</button> 
        <button className='theme-selector-slide-right-button'>&#10095;</button>
    </div>
  )
}
