import React, { useState } from "react";
import PageScrollProgressBar from "./SecondaryComponent/PageScrollProgressBar";
import Home from "./Home";
import Testing from "./Testing";
import Contact from "./Contact";
import About from "./About";
import Footer from "./Footer";
import Hamburger from "./SecondaryComponent/Hamburger"
import ThemeColorSelector from "./SecondaryComponent/ThemeColorSelector";
import Notification from "./SecondaryComponent/Notification";

import logo from "./assets/images/logos/logo500.png";
import "./assets/css/custom.css";
import "./Javascript.js"
import { BrowserRouter as Router, Route, Link, Routes} from "react-router-dom";

// import Demo from "./Demo";
import video from "./assets/videos/Forest.mp4";

export default function Navbar() {

const [nav,setNav]=useState("hide")

// setTimeout(handler)
let navToggler=(e)=>{
  return document.querySelector("#navbarSupportedContent").classList.toggle("smallScreenNavbarMenu")
}


  return (
    <div className="fade-in navbar-main-div">
      <script src={"./Javascript.js"}></script>
      {/* background Video and filter  */}
      <video src={video} muted loop autoPlay className="websiteBackgroundVideo"></video>
      <div className="websiteBackgroundVideoOverlay"></div> 
      {/* <ToastContainer position="bottom-center" autoClose={1500} hideProgressBar={false} newestOnTop closeOnClick rtl={false} pauseOnFocusLoss draggable pauseOnHover/> */}
      <Notification/>
      <PageScrollProgressBar/>
      <ThemeColorSelector/>
      
      <div>
        <Router>
          <nav className="navbar navbar-expand-md sticky-top ">
            <div className="container-fluid">
                <Link className="" to="">
                  <img src={logo} alt="logo" width="60px" height="60px" />
                </Link>
                
              <div>
              <button
                  className="navbar-toggler"
                  type="button"
                  onClick={navToggler}>
                        <Hamburger/>
                  </button>
                <div
                  className=""
                  id="navbarSupportedContent"
                >
                  <ul className="navbar-nav mb-lg-0">
                    <li className="nav-item">
                      <Link className="nav-link navbarLink active" to="/home">
                        Home
                      </Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link navbarLink active" to="/about">
                        About
                      </Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link navbarLink active" to="/contact">
                        Contact
                      </Link>
                    </li>
                    <li className="nav-item">
                      <a
                        className="nav-link navbarLink active"
                        href="/resume/AnkitMishraCV.pdf"
                        download
                      >
                        Resume
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>

          </nav>

          <Routes>
            <Route exact path="/" element={<Home />}/>
            <Route path="/home" element={<Home />} />
            <Route path="/about" element={<About />} />
            <Route path="/contact" element={<Contact />} />
          </Routes>
        </Router>
      </div>
      <Footer />
    </div>
  );
}
