import "../assets/css/custom.css";

export default function DataValidator(ele) {
  const classToggler = (condition) => {
    if (condition) {
      ele.classList.add("fine");
      ele.classList.remove("not-fine");
      ele.setAttribute("data-checked","true")
    } else {
      ele.classList.add("not-fine");
      ele.classList.remove("fine");
      ele.setAttribute("data-checked","false")
    }
  };

  var condition;
  // condition for name
  if (ele.name === "name") {
    condition = ele.value.length >= 1 ? true : false;
    classToggler(condition);
  }
  // condition for email

  if (ele.name === "email") {
    let re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    condition = re.test(ele.value);
    classToggler(condition);
  }

  // condition for mobile
  if (ele.name === "mobile") {
    let re = /^(\+\d{1,3}[- ]?)?\d{10}$/;
    condition = re.test(ele.value);
    classToggler(condition);
  }

  // condition for Subject
  if (ele.name === "subject") {
    condition = ele.value.length >= 1 ? true : false;
    classToggler(condition);
  }

  // condition for Subject
  if (ele.name === "description") {
    condition = ele.value.length >= 1 ? true : false;
    classToggler(condition);
  }


  //  condition for country Code
  if(ele.name === "countrycode"){
    condition = ele.value.length>0?true:false;
    classToggler(condition);
  }
  return condition;
}
