import { ToastContainer } from "react-toastify";

export default function Notification() {
    return(
    <ToastContainer
        position="bottom-center"
        // autoClose={3000}
        hideProgressBar={false}
        newestOnTop
        closeOnClick={true}
        rtl={false}
        pauseOnFocusLoss
        draggable={true}
        pauseOnHover={true}
        className="fade-in"
        style={{ width: "70%" }}
      />
      )
}
