import React from "react";
import MyImage from "./assets/images/ankitmishra1.jpg";
import "./assets/css/custom.css";
import { useRef } from "react";

import Contact from "./Contact";

export default function About() {
  return (
    <div className="fade-in about component-container">
      <div className="component-heading">About</div>
      <div className="component-body">
        <div className="component-body-left">
          <img src={MyImage} className="myImage" width="100%" alt="" />
        </div>
        <div className="component-body-right">
          <div className="component-content">
            I am Ankit Mishra. I have passion for technology since my childhood.
            <br />
            <br />
            I believe Tech is a Magic. The one who plays with it, is a magician.
            <br />
            <br />
            Problem is a key for any innovation and i love solving it. Everytime
            it inspires me to do and learn something different and better, and
            that's what make me a Tech Savvy.
            <br />
            <br />
          </div>
        </div>
      </div>
    </div>
  );
}
